.box{
    border: 2px solid black;
    height:400px;
    width:70%;
    float: left;
    margin-left: 5%;
    box-shadow: 0px 3px 10px black;
    margin-top: 2%; 
    
}

.second{
    margin-top: 2%; 
}


body{
    margin: auto;
}

 

.leftopt{
    margin-right: 3%;
}
.filter{
    height:700px;
    width:20%;
    background-color:white;
    float: left;
    border:2px solid white;
    box-shadow: 0px 3px 10px black;
    margin-top: 2%; 
    margin-left: 2%; 
    line-height: 25px;
}

.first{
    width:70%;
    float: left;
    margin-left: 5%;
}

.image{
    width:50%;
    height:50%;
    float: left;
}
.details{
    width:100%;
    height:100%;
    font-family: sans-serif;
    font-weight: bold;
    color: #192f60; 
}

.maindiv{
    width:100%;
    height:60%;
}

.moredetails{
    width:100%;
    height:30%;
    margin-left: 3%;
    font-family: cursive;
    font-weight: bold;
    color: darkslategray;
}
.imageClass{
    height:150px;
    width:70%;
    margin-left: 2%;
    border-radius: 20px;
}
h1{
    color: grey;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-style: inherit;
    font-weight: bold;
    text-decoration: inherit;
}
center{
    background: linear-gradient(to right, #00aaff , #00ff6c);
    
}
h4{
    font-weight: bolder;
    font-family: sans-serif;
    color: #192f60;
    margin-top: 20px;
    font-size: 20px;
}
.pagination a {
    color: gray;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    border: solid 1px gray;
    border-radius: 15px;
    margin: 2px;
}